import React from 'react'
import { NavLink } from 'react-router-dom'
import classes from './Header.module.css'

function Header(props) {
  //debugger
  return (
    <header className={classes.header}>
      <img
        src='https://seeklogo.com/images/C/company-name-logo-09881CAD1A-seeklogo.com.png'
        alt=''
      />
      <div className={classes.userLogin}>
        {props.auth.authData.isAuth ? (
          <p>
            <img
              src={
                props.auth.authData.avatar ||
                'https://w1.pngwing.com/pngs/950/424/png-transparent-circle-point-sphere-area-blue-cobalt-blue-azure-electric-blue.png'
              }
              style={{
                borderRadius: '50%',
                width: '20px',
                position: 'relative',
                top: '5px',
                left: '-5px',
              }}
            />
            {props.auth.authData.login}
            <button onClick={props.logout}>LogOut</button>
          </p>
        ) : (
          <div>
            <NavLink to='/login'>Login</NavLink>
          </div>
        )}
      </div>
    </header>
  )
}

export default Header
