import React from 'react'
import { NavLink } from 'react-router-dom'
import classes from './Navbar.module.css'
import FriendsContainer from '../Friends/FriendsContainer'

function Navbar() {
  return (
    <nav className={classes.nav}>
      <div className={classes.item}>
        <NavLink
          to='/profile/24068'
          className={(clsNm) =>
            clsNm.isActive ? classes.active : classes.item
          }
        >
          Profile
        </NavLink>
      </div>
      <div className={classes.item}>
        <NavLink
          to='/dialogs'
          className={(clsNm) =>
            clsNm.isActive ? classes.active : classes.item
          }
        >
          Messages
        </NavLink>
      </div>
      <div className={classes.item}>
        <NavLink
          to='/news'
          className={(clsNm) =>
            clsNm.isActive ? classes.active : classes.item
          }
        >
          News
        </NavLink>
      </div>
      <div className={classes.item}>
        <NavLink
          to='/music'
          className={(clsNm) =>
            clsNm.isActive ? classes.active : classes.item
          }
        >
          Music
        </NavLink>
      </div>
      <div className={classes.item}>
        <NavLink
          to='/settings'
          className={(clsNm) =>
            clsNm.isActive ? classes.active : classes.item
          }
        >
          Settings
        </NavLink>
      </div>
      <div className={classes.item}>
        <NavLink
          to='/users'
          className={(clsNm) =>
            clsNm.isActive ? classes.active : classes.item
          }
        >
          Users
        </NavLink>
      </div>
      <div className={classes.friends}>
        <FriendsContainer />
      </div>
    </nav>
  )
}

export default Navbar
