import { authAPI, profileAPI } from '../api/api'
const ADD_POST = 'profile/ADD-POST'
const SET_USER_PROFILE = 'profile/SET_USER_PROFILE'
const SET_USER_STATUS = 'profile/SET_USER_STATUS'

let i = 5

let initialState = {
  postsData: [
    { id: 1, message: 'Hi, my name is Dima', countLikes: 5 },
    { id: 2, message: 'Hello, my name is Oleg', countLikes: 9 },
    { id: 3, message: 'TEST', countLikes: 11 },
  ],
  postValue: '',
  profile: '',
  statusValue: 'STATUS',
}

function profileReducer(state = initialState, action) {
  switch (action.type) {
    case ADD_POST: {
      let post = {
        id: i++,
        message: action.message,
        countLikes: 0,
      }

      return {
        ...state,
        postsData: [...state.postsData, post],
      }
    }

    case SET_USER_PROFILE: {
      return {
        ...state,
        profile: action.profile,
      }
    }

    case SET_USER_STATUS: {
      return {
        ...state,
        statusValue: action.status,
      }
    }

    default:
      return state
  }
}
export default profileReducer

export const addPost = (message) => ({ type: ADD_POST, message })

export const setUserProfile = (profile) => ({ type: SET_USER_PROFILE, profile })
export const setUserStatus = (status) => ({ type: SET_USER_STATUS, status })

export const getUsersData = (userId) => {
  return async (dispatch) => {
    let data = await authAPI.getUserData(userId)
    dispatch(setUserProfile(data))
  }
}

export const getUserStatus = (userId) => {
  debugger
  return async (dispatch) => {
    let response = await profileAPI.getUserStatus(userId)
    dispatch(setUserStatus(response.data))
  }
}

export const updateUserStatus = (status) => {
  return async (dispatch) => {
    let response = await profileAPI.updateUserStatus(status)
    if (response.data.resultCode === 0) {
      dispatch(setUserStatus(status))
    }
  }
}
