import * as axsios from 'axios'

const instanceAxios = axsios.create({
  baseURL: 'https://social-network.samuraijs.com/api/1.0/',
  withCredentials: true,
  headers: {
    'API-KEY': 'd3a82c47-5595-4bcf-80b1-ed263ceeccd6',
  },
})

export const usersAPI = {
  getUsers(page, pagesCount) {
    return instanceAxios
      .get(`users?page=${page}&count=${pagesCount}`)
      .then((response) => response.data)
  },
  followUser(userId) {
    return instanceAxios
      .post(`follow/${userId}`)
      .then((response) => response.data)
  },
  unfollowUser(userId) {
    return instanceAxios
      .delete(`follow/${userId}`)
      .then((response) => response.data)
  },
}

export const profileAPI = {
  getUserData(id) {
    return instanceAxios.get('profile/' + id).then((response) => response.data)
  },
  getUserStatus(id) {
    return instanceAxios.get('profile/status/' + id)
  },
  updateUserStatus(status) {
    return instanceAxios.put('profile/status/', { status: status })
  },
}

export const authAPI = {
  authUser() {
    return instanceAxios.get('auth/me')
  },
  getUserData(id) {
    //console.warn('Use profileAPI method')
    return profileAPI.getUserData(id)
  },
  login(email, password, rememberMe = false) {
    return instanceAxios.post('auth/login', { email, password, rememberMe })
  },
  logout() {
    return instanceAxios.delete('auth/login')
  },
}
