import React, { useState, useEffect } from 'react'
import classes from './ProfileStatus.module.css'

const ProfileStatusWithHooks = (props) => {
  let [editStatus, setEditStatus] = useState(false)
  let [statusValue, setStatusValue] = useState(props.statusValue)

  const onEditStatus = (text) => {
    setStatusValue(text)
  }

  const activateStatus = () => {
    setEditStatus(true)
  }

  const deactivateStatus = () => {
    setEditStatus(false)
    props.updateUserStatus(statusValue)
  }

  useEffect(() => {
    setStatusValue(props.statusValue)
  }, [props.statusValue])

  return (
    <>
      <div className={classes.status}>
        {!editStatus ? (
          <p onDoubleClick={() => activateStatus()}>
            <span>{props.statusValue || 'No status'}</span>
          </p>
        ) : (
          <input
            value={statusValue}
            onChange={(e) => {
              onEditStatus(e.currentTarget.value)
            }}
            onBlur={() => deactivateStatus()}
            autoFocus
          />
        )}
      </div>
    </>
  )
}

export default ProfileStatusWithHooks
