import React from 'react'
import { connect } from 'react-redux'
import { Navigate } from 'react-router-dom'
import { reduxForm } from 'redux-form'
import { withAuthRedirect } from '../../hoc/withAuthRedirect'
import { login } from '../../redux/authReducer'
import { required } from '../../utils/validate/validate'
import { Input, createField } from '../Common/FormsControls/FormsControls'
import styles from './../Common/FormsControls/FormsControls.module.css'

function LoginForm({ handleSubmit, error }) {
  return (
    <form onSubmit={handleSubmit}>
      {createField('email', 'email', Input, [required])}
      {createField('password', 'password', Input, [required], 'password')}
      {createField('', 'rememberMe', Input, [], 'checkbox', 'remember me')}
      {error ? <div className={styles.errorValidation}>{error}</div> : ''}
      <div>
        <button>Login</button>
      </div>
    </form>
  )
}

let LoginReduxForm = reduxForm({ form: 'login' })(LoginForm)

function Login({ login, isAuth }) {
  function onSubmit(formData) {
    login(formData.email, formData.password, formData.rememberMe, true)
  }
  if (isAuth) {
    return <Navigate to='/profile' />
  }
  return (
    <>
      <h1>LOGIN</h1>
      <LoginReduxForm onSubmit={onSubmit} />
    </>
  )
}

let mapStateToProps = (state) => {
  return {
    isAuth: state.auth.isAuth,
  }
}

export default connect(mapStateToProps, { login })(Login)
