import { connect } from 'react-redux'
import React from 'react'
import {
  follow,
  unfollow,
  setCurrentPage,
  setTotalCurrentUsers,
  toggleFollowing,
  requestUsers,
} from '../../redux/usersReducer'
import Users from './Users'
import { withAuthRedirect } from '../../hoc/withAuthRedirect'
import { compose } from 'redux'
import {
  getCurrentPage,
  getIsFetching,
  getIsToggleFollowing,
  getPageSize,
  getTotalUsersCount,
  getUsers,
} from '../../redux/usersSelectors'

class UsersContainer extends React.Component {
  componentDidMount() {
    this.props.getUsers(this.props.currentPage, this.props.pagesCount)
  }
  onChangeUsers = (page) => {
    this.props.getUsers(page, this.props.pagesCount)
  }

  render() {
    return (
      <Users
        totalUsersCount={this.props.totalUsersCount}
        pageSize={this.props.pageSize}
        currentPage={this.props.currentPage}
        onChangeUsers={this.onChangeUsers}
        users={this.props.users}
        follow={this.props.follow}
        unfollow={this.props.unfollow}
        isFetching={this.props.isFetching}
        isToggleFollowing={this.props.isToggleFollowing}
      />
    )
  }
}

let mapStateToProps = (state) => ({
  users: getUsers(state),
  pageSize: getPageSize(state),
  totalUsersCount: getTotalUsersCount(state),
  currentPage: getCurrentPage(state),
  isFetching: getIsFetching(state),
  isToggleFollowing: getIsToggleFollowing(state),
})

export default compose(
  connect(mapStateToProps, {
    follow,
    unfollow,
    setCurrentPage,
    setTotalCurrentUsers,
    toggleFollowing,
    getUsers: requestUsers,
  }),
  withAuthRedirect
)(UsersContainer)
