import React from 'react'
import { useState } from 'react'
import classes from './Paginator.module.css'

function Paginator({
  totalItemsCount,
  pageSize,
  currentPage,
  onChangeUsers,
  paginatorSize,
}) {
  let pagesCount = Math.ceil(totalItemsCount / pageSize)
  let pages = []
  for (let i = 1; i <= pagesCount; i++) {
    pages.push(i)
  }
  let portionCount = Math.ceil(pagesCount / paginatorSize)
  let [portionNumber, setPortionNumber] = useState(1)
  let leftPortionPageNumber = (portionNumber - 1) * paginatorSize + 1
  let rightPortionPageNumber = portionNumber * paginatorSize

  return (
    <>
      <div>
        {portionNumber > 1 && (
          <button onClick={() => setPortionNumber(portionNumber - 1)}>
            PREV
          </button>
        )}
        {pages
          .filter(
            (p) => p >= leftPortionPageNumber && p <= rightPortionPageNumber
          )
          .map((page, i) => {
            return (
              <span
                className={currentPage === page ? classes.currentPage : ''}
                key={i}
                onClick={() => onChangeUsers(page)}
              >
                {page}
              </span>
            )
          })}
        {portionCount > portionNumber && (
          <button onClick={() => setPortionNumber(portionNumber + 1)}>
            NEXT
          </button>
        )}
      </div>
    </>
  )
}

export default Paginator
