import { usersAPI } from '../api/api.js'
import { updateObjectInArray } from '../utils/object-helpers.js'

const FOLLOW = 'users/FOLLOW'
const UNFOLLOW = 'users/UNFOLLOW'
const SET_USERS = 'users/SET_USERS'
const SET_CURRENT_PAGE = 'users/SET_CURRENT_PAGE'
const SET_USER_COUNT = 'users/SET_USER_COUNT'
const TOGGLE_IS_FETCHING = 'users/TOGGLE_IS_FETCHING'
const TOGGLE_IS_FOLLOWING = 'users/TOGGLE_IS_FOLLOWING'

let initialState = {
  users: [],
  pageSize: 1,
  totalUsersCount: 1,
  currentPage: 1,
  isFetching: true,
  isToggleFollowing: [],
}

function usersReducer(state = initialState, action) {
  switch (action.type) {
    case FOLLOW: {
      return {
        ...state,
        users: updateObjectInArray(state.users, action.userId, 'id', {
          followed: true,
        }),
      }
    }
    case UNFOLLOW: {
      return {
        ...state,
        users: updateObjectInArray(state.users, action.userId, 'id', {
          followed: false,
        }),
      }
    }
    case SET_USERS: {
      return {
        ...state,
        users: [...action.users],
      }
    }
    case SET_CURRENT_PAGE: {
      return {
        ...state,
        currentPage: action.page,
      }
    }
    case SET_USER_COUNT: {
      return {
        ...state,
        totalUsersCount: action.totalUsers,
      }
    }
    case TOGGLE_IS_FETCHING: {
      return {
        ...state,
        isFetching: action.isFetching,
      }
    }
    case TOGGLE_IS_FOLLOWING: {
      return {
        ...state,
        isToggleFollowing: !action.isFetching
          ? state.isToggleFollowing.filter((id) => id !== action.userId)
          : [...state.isToggleFollowing, action.userId],
      }
    }

    default:
      return state
  }
}
export default usersReducer
export const followSuccess = (userId) => ({ type: FOLLOW, userId: userId })
export const unfollowSuccess = (userId) => ({ type: UNFOLLOW, userId: userId })
export const setUsers = (users) => ({ type: SET_USERS, users: users })
export const setCurrentPage = (page) => ({ type: SET_CURRENT_PAGE, page })
export const setTotalCurrentUsers = (userCount) => ({
  type: SET_USER_COUNT,
  totalUsers: userCount,
})
export const toggleIsFetching = (isFetching) => {
  return {
    type: TOGGLE_IS_FETCHING,
    isFetching,
  }
}
export const toggleFollowing = (isFetching, userId) => {
  return {
    type: TOGGLE_IS_FOLLOWING,
    isFetching,
    userId,
  }
}

export const requestUsers = (currentPage, pagesCount) => {
  return async (dispatch) => {
    dispatch(toggleIsFetching(true))
    let data = await usersAPI.getUsers(currentPage, pagesCount)
    dispatch(toggleIsFetching(false))
    dispatch(setUsers(data.items))
    dispatch(setTotalCurrentUsers(data.totalCount))
    dispatch(setCurrentPage(currentPage))
  }
}

let followUnfollowFlow = async (dispatch, userId, apiMethod, actionCreator) => {
  dispatch(toggleFollowing(true, userId))
  let data = await apiMethod(userId)
  if (data.resultCode === 0) {
    dispatch(actionCreator(userId))
  }
  dispatch(toggleFollowing(false, userId))
}

export const follow = (userId) => {
  return (dispatch) => {
    followUnfollowFlow(
      dispatch,
      userId,
      usersAPI.followUser.bind(usersAPI),
      followSuccess
    )
  }
}

export const unfollow = (userId) => {
  return (dispatch) => {
    followUnfollowFlow(
      dispatch,
      userId,
      usersAPI.unfollowUser.bind(usersAPI),
      unfollowSuccess
    )
  }
}
