const ADD_NEW_MESSAGE = 'dialog/ADD-NEW-MESSAGE'

let i = 5
let initialState = {
  dialogData: [
    { id: 1, name: 'Dimich' },
    { id: 2, name: 'Andrey' },
    { id: 3, name: 'Sveta' },
    { id: 4, name: 'Sasha' },
    { id: 5, name: 'Viktor' },
    { id: 6, name: 'Valera' },
  ],
  messagesData: [
    {
      id: 1,
      message: 'hi',
      avatar:
        'http://pm1.narvii.com/6889/74979d4d2744ec6e27995b6e866f091d04c0b40cr1-515-414v2_uhq.jpg',
    },
    {
      id: 2,
      message: 'How is your blala',
      avatar:
        'http://zakaz-kartiny.ru/upload/resize_cache/iblock/e15/600_400_1/e157ad78a8ce2106c072a7d4575927ca.jpg',
    },
    {
      id: 3,
      message: 'Yo',
      avatar:
        'http://zakaz-kartiny.ru/upload/resize_cache/iblock/cbb/600_400_1/cbb81e84c8fbbf952c6df7965c6c4eaf.jpg',
    },
  ],
  messageValue: '',
}

function dialogReducer(state = initialState, action) {
  switch (action.type) {
    case ADD_NEW_MESSAGE: {
      let post = {
        id: i++,
        message: action.message,
        avatar:
          'http://zakaz-kartiny.ru/upload/resize_cache/iblock/cbb/600_400_1/cbb81e84c8fbbf952c6df7965c6c4eaf.jpg',
      }

      return {
        ...state,
        messagesData: [...state.messagesData, post],
      }
    }

    default:
      return state
  }
}
export default dialogReducer

export const addNewMessage = (message) => ({ type: ADD_NEW_MESSAGE, message }) 
