import { stopSubmit } from 'redux-form'
import { authAPI } from '../api/api'

const SET_USER_DATA = 'auth/SET_USER_DATA'
const SET_USER_AVATAR = 'auth/SET_USER_AVATAR'
const LOGIN_USER = 'auth/LOGIN_USER'

let initialState = {
  id: null,
  email: null,
  login: null,
  isAuth: false,
  avatar: null,
}

export default function authReducer(state = initialState, action) {
  switch (action.type) {
    case SET_USER_DATA: {
      return {
        ...state,
        ...action.payload,
      }
    }
    case SET_USER_AVATAR: {
      return {
        ...state,
        avatar: action.avatar,
      }
    }
    case LOGIN_USER: {
    }

    default:
      return state
  }
}

export const setUserData = (id, email, login, isAuth) => ({
  type: SET_USER_DATA,
  payload: { id, email, login, isAuth },
})
export const setUserAvatar = (avatar) => ({
  type: SET_USER_AVATAR,
  avatar,
})

export const authUser = () => {
  return async (dispatch) => {
    let response = await authAPI.authUser()
    if (response.data.resultCode === 0) {
      let { id, email, login } = response.data.data
      dispatch(setUserData(id, email, login, true))
      let data = await authAPI.getUserData(response.data.data.id)
      dispatch(setUserAvatar(data.photos.small))
    }
  }
}

export const login = (email, password, rememberMe) => {
  return async (dispatch) => {
    let response = await authAPI.login(email, password, rememberMe)
    console.log('login')
    if (response.data.resultCode === 0) {
      dispatch(authUser())
    } else {
      let message =
        response.data.messages.length > 0 ? response.data.messages[0] : ''
      dispatch(stopSubmit('login', { _error: message }))
    }
  }
}

export const logout = () => {
  return async (dispatch) => {
    let response = await authAPI.logout()
    if (response.data.resultCode === 0) {
      dispatch(setUserData(null, null, null, false))
    }
  }
}
