import React from 'react'
import Post from './Post/Post'
import classes from './MyPosts.module.css'
import { Field } from 'redux-form'
import { reduxForm } from 'redux-form'
import {
  maxValueLength,
  required,
  minValueLength,
} from '../../../utils/validate/validate'
import { Textarea } from '../../Common/FormsControls/FormsControls'

let maxLength10 = maxValueLength(10)
let minLength5 = minValueLength(5)

let MyPostsMessagesRedux = reduxForm({ form: 'myPostsMessages' })(
  MyPostsMessages
)

function MyPostsMessages(props) {
  return (
    <form onSubmit={props.handleSubmit}>
      <Field
        component={Textarea}
        name='message'
        validate={[required, maxLength10, minLength5]}
        placeholder='test'
      />
      <button></button>
    </form>
  )
}

const MyPosts = React.memo((props) => {
  let postsElements = props.postsElements.map((post) => (
    <Post key={post.id} message={post.message} likes={post.countLikes} />
  ))

  let submit = (data) => {
    props.addPost(data.message)
  }

  return (
    <div className={classes.postsBlock}>
      <h3>My posts</h3>
      <MyPostsMessagesRedux onSubmit={submit} />
      <div className={classes.posts}>New post</div>
      {postsElements}
    </div>
  )
})

export default MyPosts
