import React from "react";
import classes from "./Post.module.css";

function Post(props) {
  return (
    <>
      <div className={`${classes.item}`}>
        <img
          src="https://cspromogame.ru//storage/upload_images/avatars/788.jpg"
          alt=""
        />
        <p>{props.message}</p>
        <p style={{ color: "yellow" }}>{props.likes} </p>
        <p className={classes.like}>like</p>
      </div>
    </>
  );
}

export default Post;
